.sortBtn {
  background-color: rgba(255, 255, 255, 0);
  cursor: pointer;

  svg {
    margin-right: 10px;
    font-size: 18px;
    color: #333333;
    transform: translateY(14%);
  }

  span {
    font-family: Jura, sans-serif;
    font-size: 18px;
    color: #333333;
  }
}

.sortBtn[categories='true'] {
  svg {
    margin-right: 10px;
    font-size: 16px;
    color: #333333;
    transform: translateY(14%);
  }

  span {
    font-family: Jura, sans-serif;
    font-size: 16px;
    color: #333333;
  }
}
